
import React from "react";
import {Button, f7, Navbar, Page, PageContent, Row} from "framework7-react";
import {goodsSearchRouter} from "../goodssearch/goodsSearchRouter";
import {ComponentBase} from "../components/ComponentBase";
import {goodsDetailPageRouter} from "../goodsdetail/goodsDetailPageRouter";
import {showSimpleToast} from "../../uiuitils/toast";

const ClipboardJS = require("clipboard");
let clipboard;
export class DiscountPageContent extends React.Component{
    constructor(props) {
        super(props);
        this.state = {};
        this._addEvents();
    }
    _addEvents = ()=>{

    }
    _onSearchbarFocusIn = ()=>{
        document.activeElement.blur();
        // console.log('f7.views.main.router is', f7.views.main.router);
        // f7.views.main.router.navigate({"path":"/goodssearch"})
        f7.views.main.router.navigate({"path":goodsSearchRouter.path})
        // f7.views.main.router.navigate({"path":goodsDetailPageRouter.path})

    }
    render = ()=>{
        return (
            <div className="navbar">
                <div className="navbar-bg"></div>
                <div className="navbar-inner sliding">
                    <form className="searchbar" style={{width:"100%"}}>
                        <div className="searchbar-inner">
                            <div className="searchbar-input-wrap">
                                <input onFocus={this._onSearchbarFocusIn} type="search" placeholder="商品链接、标题或关键字" />
                                <i className="searchbar-icon"></i>
                                <span className="input-clear-button"></span>
                            </div>
                            <span className="searchbar-disable-button">Cancel</span>
                        </div>
                    </form>
                </div>
            </div>)
    }
}

let buttonIndex = 0;
export class DiscountPage extends ComponentBase{
    constructor(props) {
        super(props);
    }
    _loadMore = ()=>{
        console.log('load more')
    }
    _onEmpty = (e) => {
        console.log("eeeeeeeeeeee", e);
    }
    _onCopyWeixinClick = ()=>{
        showSimpleToast("微信号已复制，快去加好友领撒币金吧！！！")
    }
    render = ()=>{
        let cn = "infinite-scroll-content infinite-scroll-top";
        cn = ""
        const btnName = "weixinCopyBtn" + (++buttonIndex);
        setTimeout(()=>{
            new ClipboardJS(`.${btnName}`);
        })
        return <Page pageContent={false}>
            <DiscountPageContent />
            <PageContent className={cn}
                         // infinite
                         // infiniteDistance={50}
                         // infinitePreloader={showPreloader}
                         // onInfinite={this._loadMore}
            >
                <div className={"block"}>
                    <div>我们提供电商平台里找不到的隐藏优惠券以及大多数商品提供现金补贴的福利。</div>
                    <div>我们的使命是：您购物，我撒币！</div>
                    <div>您只需把电商平台要购买的商品链接、标题复制下来，粘贴到上面搜索框，就能看到这个商品有没有优惠和我们给您的补贴。</div>
                    <div>或者您也可以用关键字直接搜索。</div>
                    <hr/>
                    <div>补贴分两部分：</div>
                    <ul>
                        <li>
                            第一部分，京东的商品购买前可以在这里先领取补贴金，在购买时直接当现金使用。其它平台正在陆续添加中。
                        </li>
                        <li>
                            第二部分，在您购买的商品收货后，且过了退换货期，各电商平台在20号-月底期间会跟我们结算，结算后，我们会在微信、QQ群里给您发放定向红包，请注意查收奥~~~
                        </li>
                    </ul>
                    <hr/>
                    <div>小提示：</div>
                    <ul>
                        <li>
                            您有大件或者不急着买的商品可以让群里的谍报员帮您观察，遇到打折时我们会及早通知您。譬如京东每天都会有多种商品是日常价5折甚至3折以下，淘宝和拼多多也有百亿补贴的惊喜价。这样可以让您避免买了吃亏的价格。
                        </li>
                        <li>
                            几大平台里红包幅度最大的是唯品会，其次是京东。
                        </li>
                        <li>爆款价和零元购和福利购最多的是京东。</li>
                    </ul>
                    <hr/>
                    <Row>
                        <div>领红包微信号：baobeidaola</div>
                        <div><Button className={btnName} onClick={this._onCopyWeixinClick}
                                     data-clipboard-text={"baobeidaola"}>复制</Button></div>
                    </Row>
                </div>
            </PageContent>
        </Page>
    }
}
